import Box from "@mui/material/Box";
import IndexPage from "./IndexPage";
import Second_page from "./Second_page";
import BottomPage from "./BottomPage";
import DetailPage from "./DetailPage";
import Error from "./Error";
import LoginPage from "./LoginPage";

import {
    Route,
    Routes,
    Navigate ,
} from 'react-router-dom'

function Page() {

    return (
        <Box sx={{
            minWidth: "93%",
            minHeight: "100vh",
            background: "#e8e4e3",
            pl: 5,
        }}>
            <Routes>
                <Route path="/naval_ships-new/" element={<Navigate to="/naval_ships-new/LoginPage"/>} />
                <Route path="/naval_ships-new/LoginPage" element={<LoginPage />} />
                <Route path="/naval_ships-new/IndexPage" element={<IndexPage />} />
                <Route path="/naval_ships-new/Second_page" element={<Second_page />} />
                <Route path="/naval_ships-new/BottomPage/:ID" element={<BottomPage />} />
                <Route path="/naval_ships-new/DetailPage/:time/:ProdID" element={<DetailPage />} />
                <Route path="" element={<Error />} />
            </Routes>
        </Box>
    );
}

export default Page;
